import React, { Component } from 'react';
import { render } from 'react-dom';
import { BrowserRouter, Route, Link } from 'react-router-dom';


import SecondPage from './SecondPage';

const FirstPage = () => {
  return <>
  <h1>Первая страница</h1> <Link to="/" >В корень</Link>
     
  </>
};

class App extends Component {
  name = 'React'

  render() {
    return (
      <>
        <div className="App">
          <h1>Привет</h1>
          
          <BrowserRouter>
          <Link to="/first" >Первая страница</Link> и 
          <Link to="/second" > Вторая страница</Link>
          <Route exact path="/first" component={FirstPage} /> 
          <Route exact path="/second" component={() => <SecondPage name={this.name} />} />

          </BrowserRouter>
        </div>

      </>
    );
  }
}

render(<App />, document.getElementById('root'));


 
 
